.container {
  border: 1px solid black;
  border-radius: 5px;
  width: 83.34%;
  height: 26.6%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  margin-bottom: 20%;
}

.innerContainer {
  display: flex;
  flex-wrap: wrap;
  width: 75.8%;
  height: 60.5%;
  position: relative;
  top: 0;
  left: 0;
}

.nextFigureText {
  position: relative;
  top: -5px;
  font-size: 1.4rem;
  font-weight: bold;
}
