.container {
  justify-content: space-between;
  width: 560px;
}
.arrowsContainer {
  flex-direction: column;
  width: 62.5%;
  padding: 3%;
}
.leftRightContainer {
  justify-content: space-between;
  margin-bottom: 15%;
}
.downContainer {
  justify-content: center;
}
.rotateContainer {
  justify-content: center;
  align-items: center;
  width: 37.5%;
  padding-top: 15%;
}
.container,
.arrowsContainer,
.leftRightContainer,
.downContainer,
.rotateContainer {
  display: flex;
}

@media (max-width: 767px) {
  .container {
    width: 90%;
  }
}

