.button {
  border-radius: 50%;
  background-color: #ffeb3b;
  background-size: 70%;
  background-repeat: no-repeat;
  background-position: center;
  box-shadow: 0px 0px 5px black;
  border: none;
  transition: all 0.1s;
  padding: 17%;
}

.button:active {
  scale: 0.9;
  box-shadow: 0px 0px 25px black;
}

.button:disabled {
  pointer-events: none;
}
