.container {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: transparent;
}

.button {
  background: #64f564;
  padding: 3% 10%;
  border: 2px solid green;
  border-radius: 5px;
  font-size: 2rem;
  font-weight: bold;
  box-shadow: 0px 0px 5px black;
}
.button:active {
  scale: 0.9;
  box-shadow: 0px 0px 25px black;
}
